@import "../../index.scss";

.project-field {
  display: grid;
  grid-template-columns: 44px 2fr 1fr 1fr 1fr 130px;
  justify-content: center;
  align-items: center;
  text-align: center;

  border-bottom: 3px solid #f8f8f8;
  border-left: 3px solid #f8f8f8;
  background-color: white;
  height: 40px;
  padding: 0 20px 0 20px;

  h5 {
    display: block;
    margin: 0px 0 0 0;
    /* text-align: center; */
  }

  .inputProjectField {
    border-bottom: none;
    font-size: 13px;
    font-weight: 700;
    width: 100%;
    text-align: center;
    color: red;
    // max-width: 100px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
      "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
      "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
  }

  input:nth-child(2),
  h5:nth-child(2) {
    text-align: left;
  }

  h5:nth-child(3) {
    transition: transform 0.1s ease-in-out;
    cursor: pointer;
  }

  h5:nth-child(3):hover {
    transform: scale(1.2);
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  }

  .buttonField {
    all: unset;
    width: 24px;
    // margin-right: 20px;
  }

  .projectFiledButtonsBlock {
    padding: 0 24px 0 24px;
    display: flex;
    justify-content: space-between;
  }
}

.project-field:hover {
  display: grid;
  grid-template-columns: 44px 2fr 1fr 1fr 1fr 130px;
  justify-content: center;
  align-items: center;
  text-align: center;

  border-bottom: 3px solid #f8f8f8;
  border-left: 3px solid #f8f8f8;
  background-color: white;
  color: $cerryColor;
  height: 40px;
  padding: 0 20px 0 20px;
}
