.log-button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  width: 100px;
}

.log-button:hover {
  cursor: pointer;
  background-color: #ffffff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  color: #83183d;
  border-radius: 5px;
}

.popupLogIn {
  display: flex;
  flex-direction: column;
  position: fixed;
  z-index: 999;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  // padding: 20px;
  height: 590px;
  width: 430px;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  .x {
    height: 32px;
    position: absolute;
    top: 20px;
    right: 20px;
  }

  .x:hover {
    cursor: pointer;
    background-color: #ffffff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
  }
}

.googleAuthButton {
  display: flex;
  align-items: center;
  gap: 15px;
  height: 50px;
  width: 280px;
  margin-top: 30px;
  padding: 20px 10px;
  .googleIcon {
    height: 24px;
    width: 24px;
  }
}

.googleAuthButton:hover {
  cursor: pointer;
  background-color: #ffffff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

.show {
  display: block;
}

.hide {
  display: none;
}
